.header {
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-left: 3px solid #606e85;
  border-right: 3px solid #606e85;
  border-bottom: 3px solid #606e85;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 0 1.5rem white;
}

.header h1 {
  width: 100%;
  text-shadow: 0 0 1rem whitesmoke;
  text-align: center;
}
