.body {
  width: 60%;
  height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 3rem 0;
  padding: 2rem;
  border: 3px solid #606e85;
  border-radius: 1rem;
  box-shadow: 0 0 1.5rem white;
}

.body-main {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.body-left {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.body-left * {
  width: 100%;
  height: 25%;
  background: whitesmoke;
  border-radius: 0.5rem;
  color: rgb(75, 75, 75);
  padding: 3rem 0;
  cursor: pointer;
  font-size: 3rem;
  text-align: center;
  margin: 0.7rem 0;
  transition: 0.3s ease-in-out;
}

.body-left *:hover {
  background: rgb(75, 75, 75);
  color: whitesmoke;
  transition: 0.3s ease-in-out;
}

.body-right {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.body-right p {
  width: 60%;
  background: whitesmoke;
  border-radius: 0.5rem;
  color: rgb(75, 75, 75);
  padding: 1.2rem 0;
  font-size: 3rem;
  text-align: center;
  margin: 0.7rem 0;
}

.body-bottom {
  background: whitesmoke;
  color: rgb(75, 75, 75);
  font-size: 3rem;
  border-radius: 0.5rem;
  padding: 0 1rem;
  margin: 1rem 0 0.5rem;
}
