.footer {
  width: 100%;
  display: flex;
  position: absolute;
  justify-content: space-between;
  align-items: center;
  bottom: 0;

  border-left: 3px solid #606e85;
  border-right: 3px solid #606e85;
  border-top: 3px solid #606e85;
  padding: 1.5rem;
  box-shadow: 0 0 1.5rem white;
}

.footer p {
  margin: 0 auto;
  font-size: 1.5rem;
  text-shadow: 0 0 1rem whitesmoke;
  font-weight: bold;
}

.footer button {
  position: absolute;
  right: 1rem;
  cursor: pointer;
  padding: 0.5rem 1.5rem;
  border-radius: 0.5rem;
  outline: none;
  font-weight: bold;
  background-color: #606e85;
}
