.modal {
  background: rgba(255, 255, 255, 0.2);
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  color: black;
}

.modal-inner {
  background: whitesmoke;
  width: 50%;
  border-radius: 1rem;
  box-shadow: 0 0 1rem black;
}

.title {
  display: flex;
  justify-content: space-between;
}

.title h1 {
  margin: 1rem auto 0 1rem;
}

.title button {
  margin: 1rem 1rem 0 auto;
  width: 3rem;
  font-size: 1.5rem;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

.rules {
  display: flex;
  align-items: center;
  justify-content: center;
}

.rules img {
  width: 100%;
}
